import ajax from './ajax'

const Base = "http://172.16.13.73:8222"
export const LOGIN = (userName,password) => ajax(Base+"/login",{userName,password},'GET')               //登录

/*客户端*/
//视频
export const REQVIDEO = (data) => ajax(Base+"/video/videos",data,'GET')                                       //获取视频列表
export const REQONEVIDEO = (data) => ajax(Base+"/video/video",data,'GET')                                     //根据ID获取单一视频

//课件
export const REQFILES = (data) => ajax(Base+"/course/courses",data,'GET')                                      //获取课件列表
export const REQFILE = (data) => ajax(Base+"/course/course",data,'GET')                                       //获取单一课件


export const REQCOMMENT = (data) => ajax(Base+"/comment/comment",data,'GET')                                  //获取评论列表
export const UPLOADCOMMENT = (data) => ajax(Base+"/comment/comment",data,'POST')                              //上传评论


/*管理端*/
//视频
export const UPLOADVIDEO = (data) => ajax(Base+"/video/video",data,'POST')                                    //上传视频
export const UPDATEVIDEO = (data,videoId) => ajax(Base+"/video/video"+videoId,data,'PUT')                     //修改视频信息
//课件
export const UPLOADFILE = (data) => ajax(Base+"/course/course",data,'POST')                                   //上传课件



